html, body
{
    height: 100vh;
    width: 100vw;
}

.calendar-screen
{
    display: flex;
    flex-flow: column;
    height: 100vh;
}

.rbc-calendar
{
    height: 100%;
}


.fab
{
    border-radius: 100%;
    bottom: 25px;
    font-size: 30px;
    padding: 25px;
    position: fixed;
    right: 25px;
}

.fab-danger
{
    bottom: 25px;
    padding: 10px;
    position: fixed;
    left: 25px;
};
